import React, { Component } from 'react'
import './Win.css'
export default class Win extends Component {
    constructor(props){
        super(props);
        this.state={
            vx:0,
            nowTy:8,
            nowTx:8,
            fullSize:false
        }
    }
    componentDidMount(){
        this.setState({
            vx:0
        })
    }
    render() {
        let state = {
            tX:8,
            tY:8
        };
     
        return (
            <div className="Win" 
            >
                <div className='head' draggable={true} onDragStart={
                e=>{
                    state.preX = e.clientX;
                    state.preY = e.clientY;
                    console.log(
`
Client: ${e.clientX}, ${e.clientY}
postition: ${state.tX}, ${state.tY}

 `);
                    
                }
                
               
                
            }
            onDragEnd={
                e=>{
                  let cX = e.clientX - state.preX,
                  cY = e.clientY - state.preY;
                    state.nowX = state.tX + cX;
                    state.nowY = state.tY + cY;

                    state.tX = state.nowX;
                    state.tY = state.nowY;
                    e.target.parentElement.style.transform = `translate(${state.nowX}px,${state.nowY}px)`;
                    console.log(
 `
Client: ${e.clientX}, ${e.clientY}  | ${state.preX}, ${state.preY}
postition: ${state.tX}, ${state.tY} | now: ${state.nowX}, ${state.nowY}

change: ${cX}, ${cY}
`);
                    
                    
                }
            }>
                
                <div className="buttons">
                    <button className={"close"} onClick={
                        e=>{
                        
                           e.target.parentElement.parentElement.parentElement.remove()

                        }
                    }></button>
                    <button className={(this.state.fullSize)?'mizi':'maxi'} onClick={(this.state.fullSize)?(e=>{
                         let parent = document.querySelector('.Win');
                      
                         
                         parent.style.width="calc(50vw)";
                         parent.style.height="calc(50vh)";
                        
                         
                         this.setState({
                             fullSize:false
                         })
                    }):(e=>{
                        let parent = document.querySelector('.Win');
                       
                        parent.style.transform = 'translate(0px,0px)';
                        parent.style.width="calc(100vw - 16px)";
                        parent.style.height="calc(100vh - 16px)"
                        
                        this.setState({
                            fullSize:true
                        })
                        
                    })}></button>
                    <button className={"mini"} onClick={
                        e=>{
                            const os = document.querySelector('.OS')
                            
                            console.log(os);
                            
                           
                            os.append()
                        }
                    }></button>
                </div>
        <div className="title" onSelect={e=>{e.preventDefault()}}>{this.props.title}</div>
                </div>
        <div className={"iframe"}>
        {this.props.app}
        </div>
                
                
                <div id='resize' draggable="true" onDragStart={
                    e=>{
                        let parent = e.target.parentElement;
                        state.xBefore = e.clientX;
                        state.yBefore = e.clientY;
                        if(state.vx){
                        state.widthBefore = state.vx;
                        state.heightBefore = state.vy;
                        }
                        else{
                        state.widthBefore = parent.offsetWidth;
                        state.heightBefore = parent.offsetHeight;
                        }
                        
                        console.log("Before W H",state.widthBefore, state.heightBefore);
                
                    }
                }
                onDragEnd={
                    e=>{
            state.xAfter = e.clientX;
            state.yAfter = e.clientY;
            let parent = e.target.parentElement;
            let width = state.widthBefore + (state.xAfter - state.xBefore);
            let height = state.heightBefore + (state.yAfter - state.yBefore);
            state.vx = Math.abs( width);
            state.vy = Math.abs(height);
            parent.style.width = width+"px";
            parent.style.height = height+"px";
            console.log("Change W H",state.xAfter - state.xBefore, state.yAfter - state.yBefore);
            console.log("After W H",width, height);
    
                       
                    }
                }
                ></div>
                </div>
           
        )
    }
}
