import React, { Component } from 'react'
import './TaskBar.css'
export default class TaskBar extends Component {
    constructor(props){
        super(props);
        this.state={
            value:props.value
        }
    }
    render() {
        let s = this.state;
        return (
            <div className="TaskBar">
                <div className='Start'></div>
            </div>
        )
    }
}
